// src/context/OrderContext.js
'use client'

import React, { createContext, useContext } from 'react'
import { useLocalStorage } from './useLocalStorage'
import { useAuth } from './AuthContext' // <--- AuthContextni ulaymiz

const OrderContext = createContext({})

export function useOrders() {
	return useContext(OrderContext)
}

export function OrderProvider({ children }) {
	const { user } = useAuth()
	const [allOrders, setAllOrders] = useLocalStorage('order-history', [])

	// 1. Yangi buyurtma qo'shish (Telefon raqami bilan birga)
	const addOrder = orderData => {
		if (!user) return // Agar user bo'lmasa buyurtma qo'shmaydi

		const newOrder = {
			id: Math.floor(100000 + Math.random() * 900000),
			userPhone: user.phone, // <--- Buyurtmani userga bog'laymiz
			...orderData,
		}
		setAllOrders(prev => [newOrder, ...prev])
	}

	// 2. FAQAT JORIY FOYDALANUVCHINING BUYURTMALARI
	// Agar login qilgan bo'lsa, uning raqamiga tegishli buyurtmalarni filtrlab beradi
	const userOrders = user
		? allOrders.filter(order => order.userPhone === user.phone)
		: []

	return (
		<OrderContext.Provider value={{ orders: userOrders, addOrder }}>
			{children}
		</OrderContext.Provider>
	)
}
