// src/context/CartContext.js
'use client'

import React, { createContext, useContext } from 'react'
import { useLocalStorage } from './useLocalStorage'

const CartContext = createContext({})

export function useCart() {
	return useContext(CartContext)
}

export function CartProvider({ children }) {
	// Savatdagi narsalarni localStorage da saqlaymiz
	const [cartItems, setCartItems] = useLocalStorage('shopping-cart', [])

	// Jami mahsulotlar soni
	const totalCartCount = cartItems.reduce(
		(total, item) => total + item.quantity,
		0
	)

	// 1. Mahsulot sonini olish
	function getItemQuantity(id) {
		return cartItems.find(item => item.id === id)?.quantity || 0
	}

	// 2. Savatga qo'shish yoki ko'paytirish
	function increaseCartQuantity(id) {
		setCartItems(currItems => {
			if (currItems.find(item => item.id === id) == null) {
				return [...currItems, { id, quantity: 1 }]
			} else {
				return currItems.map(item => {
					if (item.id === id) {
						return { ...item, quantity: item.quantity + 1 }
					} else {
						return item
					}
				})
			}
		})
	}

	// 3. Kamaytirish
	function decreaseCartQuantity(id) {
		setCartItems(currItems => {
			if (currItems.find(item => item.id === id)?.quantity === 1) {
				return currItems.filter(item => item.id !== id)
			} else {
				return currItems.map(item => {
					if (item.id === id) {
						return { ...item, quantity: item.quantity - 1 }
					} else {
						return item
					}
				})
			}
		})
	}

	// 4. Bitta mahsulotni to'liq o'chirish
	function removeFromCart(id) {
		setCartItems(currItems => currItems.filter(item => item.id !== id))
	}

	// 5. SAVATNI TO'LIQ TOZALASH (Buyurtma berilganda kerak)
	function clearCart() {
		setCartItems([])
	}

	return (
		<CartContext.Provider
			value={{
				getItemQuantity,
				increaseCartQuantity,
				decreaseCartQuantity,
				removeFromCart,
				clearCart, // <--- Muhim funksiya
				cartItems,
				totalCartCount,
			}}
		>
			{children}
		</CartContext.Provider>
	)
}
